<template>
  <div>
    <loading :active.sync="isLoading"></loading>

    <main>
      <div
        class="position-relative overflow-hidden p-3 p-md-5 text-center bg-light bgi"
      >
        <div class="col-md-5 p-lg-5 mx-auto my-5 bgw text-light rounded">
          <h1 class="display-4 fw-normal"></h1>
          <p class="lead fw-normal"></p>
        </div>
      </div>

      <div class="col-md-5 p-lg-5 mx-auto my-5 text-center">
        <p class="lead fw-normal">
           10
          <br />
        </p>
        <a href="javascript:" @click="goServiceLocation('#servicelocation')"
          ></a
        >
      </div>
      <div class="mx-auto text-center mb-5">
        <h3 class="fs-2 text fw-bold mb-3"></h3>
        <div class="row row-cols-2 row-cols-md-4 g-4 w-75 mx-auto">
          <div class="col">
            <div class="card border-0 mx-auto">
              <div class="card-body mx-auto text-center">
                <i class="bi bi-droplet-half fs-3 text"></i>
                <h5 class="card-title fw-bolder fs-4 mt-3 mb-2"></h5>
                <p class="card-text fw-bolder"></p>
              </div>
            </div>
          </div>
          <div class="col">
            <div class="card border-0">
              <div class="card-body mx-auto text-center">
                <i class="bi bi-file-text fs-3 text"></i>
                <h5 class="card-title fw-bolder fs-4 mt-3 mb-2"></h5>
                <p class="card-text fw-bolder">
                  <br />
                </p>
              </div>
            </div>
          </div>
          <div class="col">
            <div class="card border-0">
              <div class="card-body mx-auto text-center">
                <i class="bi bi-currency-dollar fs-3 text"></i>
                <h5 class="card-title fw-bolder fs-4 mt-3 mb-2"></h5>
                <p class="card-text fw-bolder">
                  <br />
                </p>
              </div>
            </div>
          </div>
          <div class="col">
            <div class="card border-0">
              <div class="card-body mx-auto text-center">
                <i class="bi bi-box-fill fs-3 text"></i>
                <h5 class="card-title fw-bolder fs-4 mt-3 mb-2"></h5>
                <p class="card-text fw-bolder">
                  <br />
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="mx-auto text-center m-5">
        <h3 id="servicelocation" class="fs-2 text fw-bold"></h3>

        <a href="https://goo.gl/maps/cQKa3ix7ZWPx4mVo6 " target="_blank"
          ><div id="map" class="w-75 mx-auto m-4"></div
        ></a>
      </div>
    </main>
  </div>
</template>

<style scoped>
#map {
  width: 50%;
  height: 300px;
}
</style>

<script>
// import L from "leaflet";
// import "leaflet/dist/leaflet.css";
import markerIcon2xUrl from "leaflet-css/dist/images/marker-icon-2x.png";
import markerShadowUrl from "leaflet-css/dist/images/marker-shadow.png";

export default {
  data() {
    return {
      isLoading: true,
    };
  },
  mounted() {
    setTimeout(() => {
      this.isLoading = false;
    }, 2000); // 2  loading overlay
    this.createMap();
  },
  methods: {
    goServiceLocation(point) {
      this.$el.querySelector(point).scrollIntoView({
        behavior: "smooth",
        block: "start",
      });
    },
    createMap() {
      let zoom = 17; // 0 - 18
      let center = [23.4805759, 120.4342358, 17]; // 
      let map = this.$L.map("map").setView(center, zoom);
      this.$L
        .tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
          attribution: " OpenStreetMap", // 
          zoomControl: true, //  - + 
        })
        .addTo(map);
      // 
      const icon = this.$L.icon({
        iconUrl: markerIcon2xUrl,
        shadowUrl: markerShadowUrl,
        iconSize: [25, 41], // 
        shadowSize: [41, 41], // 
        iconAnchor: [12, 41], // 
        shadowAnchor: [12, 41], // 
        popupAnchor: [0, -30], // 
      });
      const marker = this.$L.marker(center, { icon }).addTo(map);
      marker.bindPopup("<b></b><br>190").openPopup();
    },
  },
};
</script>
