const customRules = {
  nameRules: {
    validate: value => {
      const regex = /^[\u4e00-\u9fa5]+$/; // 
      const hasMrOrMs = value.includes('') || value.includes(''); // 
      const isValidLength = value.trim().length >= 2 && value.trim().length <= 4; //  2  4 
      return regex.test(value) && !hasMrOrMs && isValidLength;
    },
    getMessage: field => `${field} 2~4 `,
  },
  telRules: {
    validate: value => {
      const regex = /^09\d{8}$/; //  09 8 
      const isValidLength = value.trim().length === 10; //  10
      return regex.test(value) && isValidLength;
    },
    getMessage: field => `${field}  09 8 `,
  },
  addressRules:{
    validate: value => {
      const regex = /^[\u4e00-\u9fa5\d]*$/; // 
      const isValidLength = value.trim().length >= 2; // 2
      return regex.test(value) && isValidLength;
    },
    getMessage: field => `${field} `,
    
  },
  messageRules:{
    validate: value => {
      if (value.trim() === '') return true; // 
    return value.trim().length <= 25;
    },
    getMessage: field => `${field} 25 `,
    
  },
  lastfive: {
    validate: (value) => {
      return /^[0-9]+$/.test(value) && value.length >= 5;
    },
    getMessage: (field) => {
      return `${field} 5`;
    },
  },
};


export default customRules;